import React, {useState} from 'react';
import UserPool from  '../UserPool';
import { CognitoUserAttribute } from 'amazon-cognito-identity-js';
import { Link } from 'react-router-dom';

export default () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [message, setMassage] = useState('');
 
   var attributeList = [];
         
   var dataEmail = {
     Name : 'email', 
     Value : email, //get from form field
   };
   
   // var dataPersonalName = {
   //   Name : 'name', 
   //   Value : password, //get from form field
   // };
 
   var attributeEmail = new CognitoUserAttribute(dataEmail);
   // var attributePersonalName = new CognitoUserAttribute(dataPersonalName);
   
   
   attributeList.push(attributeEmail);
   //attributeList.push(attributePersonalName);
 
   const onSubmit = event => {
     event.preventDefault();
 
     UserPool.signUp(email, password, attributeList, null, (err, data) => {
       if (err) {
        setMassage('User already exits');
        console.error("efew",err);
       }
       else {
        console.log(data);
       setMassage('Registration Successfull');
       }
      });
   };
 
     return (
    //    <div>
    //    <form onSubmit={onSubmit}>
    //      <input
    //        value={email}
    //        onChange={event => setEmail(event.target.value)}
    //        placeholder = "Email address"
    //      />
 
    //      <input type='password'
    //        value={password}
    //        onChange={event => setPassword(event.target.value)}
    //        placeholder = "Password"
    //      />
 
    //      <button type='submit'>Signup</button>
    //    </form>
    //  </div>


    <div class="center">
      <h1>Registration</h1>
      <form onSubmit={onSubmit}>
        <div class="txt_field">
          {/* <input type="text" required> */}
        <input
        value={email}
        onChange={event => setEmail(event.target.value)}          
        />
        <label>Email</label>
        </div>
        <div class="txt_field">
        <input
        value={password} type="password"
        onChange={event => setPassword(event.target.value)}          
        />         {/* <input type="password" required> */}
        
        <label>Password</label>
        </div>
        
        <button type='submit' >Sign In </button>
        {/* <div class="pass">Forgot Password?</div> */}
        {/* <input type="submit" value="Login"> */}
        <div >
          {message}
        </div>

        <div class="signup_link">
          <Link to={'/'}>Already an user ...Login</Link>
        </div>
      </form>
    </div>  
     );
 };