import {BsSearch} from 'react-icons/bs';
import React, { useState, useEffect, useContext} from "react";
// import "./App.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import Modal from "react-bootstrap/Modal";
import Button from 'react-bootstrap/Button';
// import "./styles.css";
import "./LoginCss.css";
import { CognitoUser, AuthenticationDetails } from 'amazon-cognito-identity-js';
import { saveAs } from "file-saver";
import { useHistory } from "react-router-dom";
import { AccountContext } from "../components/Account";
import { Link } from "react-router-dom";
import Pool from '../UserPool';

import axios from "axios";

function App() {

  // if (!authorized){
  //   return <Redirect to="/login"/>
  // }

  const [posts, setPosts] = useState([]);
  const [value, setValue] = useState("");

  let history = useHistory();



  const onChange = (event) => {
    setValue(event.target.value);
  };

  const onSearch = (searchTerm) => {
    setValue(searchTerm);
    handleShow();
  };

  const viewDoc = (searchTerm) =>{
    let SearchUrl;

    for (let i=0; i<posts.length; i++){

      if (posts[i]['DocumentName'] == searchTerm){
        SearchUrl=posts[i]['DocumentUrl'];
        window.open(SearchUrl, '_blank');
        
        break;
      }
    }
  }

  const DownloadDoc = (searchTerm) => {
    let SearchUrl;

    for (let i=0; i<posts.length; i++){

      if (posts[i]['DocumentName'] == searchTerm){
        SearchUrl=posts[i]['DocumentUrl'];
        saveAs(SearchUrl,searchTerm);
        break;
      }
    }
  }


  useEffect(() => {
    const loadPosts = async () => {
    //   setLoading(true);
      const response = await axios.post(
        "https://84pmbhca41.execute-api.us-east-1.amazonaws.com/dev/getdocuments"
      );
      setPosts(response.data);
    //   setLoading(false);
    };

    loadPosts();
  }, []);


  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);


  const [show1, setShow1] = useState(false);

  const handleClose1 = () => setShow1(false);
  const handleShow1 = () => setShow1(true);



  return (

    
    <div class="">
      <nav class="navbar navbar-light bg-dark ">
        <a class="navbar-brand text-light" >Document Management System</a>
        <form class="form-inline">
          <button class="btn btn-outline-danger my-2 my-sm-0" onClick={()=> history.push("/")}>Logout</button>
          {/* <Button variant="primary" onClick={handleShow1}>
        Change Password
      </Button> */}
      
        </form>
      </nav>
    
      {/* <h1  style={{textAlign:"center"}}>Search</h1> */}
      <div class ="container">
        <div class = "search-bar">
      {/* <h1  style={{textAlign:"center"}}>Search</h1> */}
      {/* <form action="" class="search-bar"> */}
         <input type="text" placeholder="Search document" value={value} onChange={onChange}/>
          <Button variant="primary" onClick={() => onSearch(value)}>
      <BsSearch/>
      </Button>
        </div>
      {/* <input type="text" placeholder="Search document" name="q"> */}
      {/* <button type="submit"><img src="IMAGE location"></button> */}
      {/* </form> */}
      {/* </div> */}

      {/* <div className="search-container">
        <div className="search-inner">
          <input type="text" value={value} onChange={onChange}/>
          <Button variant="primary" onClick={() => onSearch(value)}>
        Search
      </Button>
        </div> */}
        <div className="dropdown">
          {posts
            .filter((item) => {
              const searchTerm = value;
              const fullName = item.DocumentName;

              return (
                searchTerm &&
                fullName.startsWith(searchTerm) &&
                fullName !== searchTerm
              );
            })
            .slice(0, 10)
            .map((item) => (
              <div
                onClick={() => onSearch(item.DocumentName)}
                className="dropdown-row"
                key={item.DocumentName}
              >
                {item.DocumentName}
              </div>
            ))}
        </div>
      </div>

      {/* documennt model */}
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Document</Modal.Title>
        </Modal.Header>
        <Modal.Body>You Want to view or download document</Modal.Body>
        <Modal.Footer>
          {/* <Button variant="secondary" onClick={handleClose}>
            Close
          </Button> */}
          <Button variant="primary" onClick={() => viewDoc(value)}>
            View Document
          </Button>
          <Button variant="primary" onClick={() => DownloadDoc(value)}>
            Download Document
          </Button>
        </Modal.Footer>
      </Modal>


      {/* changepassword model */}
      <Modal show={show1} onHide={handleClose1}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose1}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClose1}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    
    


    </div>
    
  );
}

export default App;
